<template>
    <div>
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-cascades"></i> 商品订单
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="handle-box">
                <el-select v-model.number="query.state" placeholder="请选择">
                    <el-option v-for="item in GoodsStateAll" :label="item.name" :key="item.value" :value="item.value"></el-option>
                </el-select>
                <el-input v-model="query.goodsName" placeholder="商品名称" class="handle-input mr10"></el-input>
                <el-button type="primary" icon="el-icon-search" @click="handleSearch">搜索</el-button>
            </div>
            <el-table
                    :data="tableData"
                    border
                    class="table"
                    ref="multipleTable"
                    header-cell-class-name="table-header"
            >
                <!--                <el-table-column type="selection" width="55" align="center"></el-table-column>-->
                <el-table-column prop="Id" label="ID" width="55" align="center"></el-table-column>
                <el-table-column prop="Seq" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="UserName" label="用户名" width="100"></el-table-column>
                <el-table-column prop="GoodsName" label="商品名称"></el-table-column>
                <el-table-column label="购买数量">
                   <template slot-scope="scope">{{scope.row.BuyCount}}</template>
                </el-table-column>
                <el-table-column label="花费积分">
                    <template slot-scope="scope">{{scope.row.BuyPoint}}</template>
                </el-table-column>

                <el-table-column label="商品单价格">
                    <template slot-scope="scope">{{scope.row.Price|money}}</template>
                </el-table-column>
                <el-table-column label="商品单价积分">
                    <template slot-scope="scope">{{scope.row.Point}}</template>
                </el-table-column>
                <el-table-column label="时间">
                    <template slot-scope="scope">{{scope.row.CreatedAt}}</template>
                </el-table-column>

                <el-table-column label="操作" width="140" align="center">
                    <template slot-scope="scope">
                        <el-button
                                type="text"
                                icon="el-icon-edit"
                                @click="handleEdit(scope.$index, scope.row)"
                        >编辑</el-button>
                        <el-button
                                type="text"
                                icon="el-icon-delete"
                                class="red"
                                @click="handleDelete(scope.$index, scope.row)"
                        >删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                        background
                        layout="total, prev, pager, next"
                        :current-page="query.pageIndex"
                        :page-size="query.pageSize"
                        :total="pageTotal"
                        @current-change="handlePageChange"
                ></el-pagination>
            </div>
        </div>


        <!-- 编辑弹出框 -->
        <el-dialog title="编辑" :visible.sync="editVisible" width="90%">
            <el-form ref="form" :model="form" :rules="rules" label-width="100px">
                <el-form-item label="商品名称" prop="GoodsName">
                    <el-input v-model="form.GoodsName"></el-input>
                </el-form-item>

                <el-form-item label="状态"  prop="State">
                    <el-select v-model.number="form.State" placeholder="请选择">
                        <el-option v-for="item in GoodsStates" :label="item.name" :key="item.value" :value="item.value"></el-option>
                    </el-select>
                </el-form-item>

                <el-form-item label="序号">
                    <el-input v-model="form.Seq"></el-input>
                </el-form-item>
                <el-form-item label="商品价格">
                    <el-input v-model.number="form.Price"></el-input>
                </el-form-item>
                <el-form-item label="兑换积分">
                    <el-input v-model.number="form.Point"></el-input>
                </el-form-item>
                <el-form-item label="商品图片">
                    <el-input v-model="form.GoodsPicUrl"></el-input>
                    <el-button>上传图片</el-button>
                    <el-button>图库</el-button>
                    <el-button>清空</el-button>
                </el-form-item>


                <el-form-item label="商品标题">
                    <el-input v-model="form.Title"></el-input>
                </el-form-item>
                <el-form-item label="摘要">
                    <el-input type="textarea" v-model="form.Content"></el-input>
                </el-form-item>

            </el-form>

            <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false">取 消</el-button>
                <el-button type="primary" @click="saveEdit">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    // import 'quill/dist/quill.core.css';
    import 'quill/dist/quill.snow.css';
    // import 'quill/dist/quill.bubble.css';
    import VueCropper  from 'vue-cropperjs';
    import { request } from '../../utils/http';
    import { quillEditor } from 'vue-quill-editor';
    import * as Quill from 'quill';


    export default {
        name: 'GoodsOrderList',
        data() {
            return {
                defaultSrc: require('../../assets/img/img.jpg'),
                fileList: [],
                imgSrc: '',
                cropImg: '',
                dialogVisible: false,
                GoodsStateAll:[
                    {name:'全部',value:0},
                    {name:'准备',value:1},
                    {name:'上架',value:2},
                    {name:'下架',value:3},
                ],
                GoodsStates:[
                    {name:'准备',value:1},
                    {name:'上架',value:2},
                    {name:'下架',value:3},
                ],
                query: {
                    state: 0,
                    goodsName: '',
                    pageIndex: 1,
                    pageSize: 10
                },
                rules: {
                    FilmName: [
                        { required: true, message: '请输入电影名称', trigger: 'blur' },
                        { min: 2, max: 50, message: '长度在 2 到 50 个字符', trigger: 'blur' }
                    ],
                    BuyLevel: [
                        { required: true, message: '请选择会员级别', trigger: 'blur' },
                        { min: 1, max: 5, message: '会员级别只能在1~5之间', trigger: 'blur' }
                    ],
                    State: [
                        { required: true, message: '请选择状态', trigger: 'blur' },
                        { min: 1, max: 3, message: '状态只能在1~3之间', trigger: 'blur' }
                    ],
                    ProjectGold: [
                        { required: true, message: '请填写项目总额', trigger: 'blur' },
                        { min: 1, message: '项目总额最少为1', trigger: 'blur' }
                    ],
                    CurVirtualPLCount: [
                        { required: true, message: '请填写已投人数', trigger: 'blur' },
                        { min: 0, message: '已投人数最少为0', trigger: 'blur' }
                    ],
                    CurVirtualProjectGold: [
                        { required: true, message: '请填写已投金额', trigger: 'blur' },
                        { min: 0, message: '已投金额最少为0', trigger: 'blur' }
                    ],
                    RebateCycle: [
                        { required: true, message: '请填写返利周期', trigger: 'blur' },
                        { min: 1, message: '返利周期最少为1', trigger: 'blur' }
                    ],
                    DailyRate: [
                        { required: true, message: '请填写日化收益', trigger: 'blur' },
                        { min: 0, message: '日化收益最少为0', trigger: 'blur' }
                    ],
                    ProjectDeadline: [
                        { required: true, message: '请填写项目期限', trigger: 'blur' },
                        { min: 1, message: '项目期限最少为0', trigger: 'blur' }
                    ],
                },

                tableData: [],
                multipleSelection: [],
                delList: [],
                editVisible: false,
                pageTotal: 0,
                form: {},
                idx: -1,
                id: -1
            };
        },
        created() {
            this.getData();
        },
        mounted() {
            // this.editor = this.$refs.myQuillEditor.quill;
        },
        beforeDestroy(){
            // this.editor = null;
            // delete this.editor;
        },
        components: {
            VueCropper,
            quillEditor
        },
        methods: {
            // 获取 easy-mock 的模拟数据
            getData() {
                request({ url:'filmbll/getgoodsorderlist',  method:'post', data:this.query }).then((res)=>{
                    if (res.code==200){
                        this.tableData = res.obj.ListData;
                        this.pageTotal = res.obj.PageTotal;
                    }
                });
            },
            // 触发搜索按钮
            handleSearch() {
                this.$set(this.query, 'pageIndex', 1);
                this.getData();
            },
            // 编辑操作
            handleEdit(index, row) {
                this.idx = index;
                console.log(row);
                this.form = row;
                this.editVisible = true;
            },
            handleAdd() {
                this.idx =-1;
                var aFilm ={

                }
                this.form = aFilm;
                this.editVisible = true;
            },
            // 保存编辑
            saveEdit() {
                if (this.idx===-1) {
                    var data = {
                        jsonData:JSON.stringify(this.form)
                    }
                    request({
                       url: 'filmbll/addgoodsinfo',  method:'post',
                        data:data
                    }).then((res)=>{
                        console.log(res);
                        if (res.code==200){
                            this.editVisible = false;
                            this.$message.success(`添加成功`);
                            this.getData();
                        } else {
                            this.$message.error('添加失败：'+res.msg);
                        }
                    });
                } else {
                    var data = {
                        jsonData:JSON.stringify(this.form)
                    }
                    request({
                        url:'filmbll/updategoods', method: 'post',
                        data:data
                    }).then((res)=>{
                        console.log(res);
                        if (res.code==200){
                            this.editVisible = false;
                            this.$message.success(`修改第 ${this.idx + 1} 行成功`);
                            this.$set(this.tableData, this.idx, this.form);
                        } else {
                            this.$message.error('更新失败：'+res.msg);
                        }
                    });

                }
            },
            // 分页导航
            handlePageChange(val) {
                this.$set(this.query, 'pageIndex', val);
                this.getData();
            }
        }
    };
</script>

<style scoped>
    .handle-box {
        margin-bottom: 20px;
    }

    .handle-select {
        width: 120px;
    }

    .handle-input {
        width: 300px;
        display: inline-block;
    }
    .table {
        width: 100%;
        font-size: 14px;
    }
    .red {
        color: #ff0000;
    }
    .mr10 {
        margin-right: 10px;
    }
    .table-td-thumb {
        display: block;
        margin: auto;
        width: 40px;
        height: 40px;
    }
    .ivu-upload {
        display: none;
    }
    .ivu-btn{

    }
</style>
